:root {
  --font-body: "Open Sans", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --main-bg:hsl(218, 28%, 13%);
  --nav-bg:hsl(217, 28%, 15%);
  --btn-gradient-1:hsl(176, 68%, 64%);
  --btn-gradient-2:hsl(198, 60%, 50%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  background-color: var(--main-bg);
  font-family: var(--font-body);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5rem;
  max-width: 69.375rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

.logo {
  margin: 1.5625rem 0 0 1.25rem;
}
@media (max-width: 39.9375em) {
  .logo {
    margin: 1.5625rem 0 0 0.625rem;
    width: 4.6875rem;
    height: 1.625rem;
  }
}

.header {
  background-color: var(--nav-bg);
  position: relative;
  height: 12vh;
}
@media (max-width: 39.9375em) {
  .header {
    height: 10vh;
  }
}

.navigation {
  display: flex;
  position: absolute;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  width: 17.125rem;
  top: 0.313rem;
  right: 3.875rem;
  max-height: 4.375rem;
}
@media (max-width: 39.9375em) {
  .navigation {
    width: 100%;
    width: 12.5rem;
    top: 0.28rem;
    right: 0.875rem;
  }
}
.navigation a {
  color: var(--white);
  font-size: 1.25rem;
  font-weight: 400;
}
@media (max-width: 39.9375em) {
  .navigation a {
    font-size: 1.125rem;
  }
}

.top__image {
  padding-top: 1.875rem;
}
.top__image img {
  width: 100%;
  width: 17.6875rem;
  height: 13.1875rem;
}
@media (min-width: 71.875em) {
  .top__image img {
    width: initial;
    height: initial;
  }
}
.top__heading {
  padding-top: 1.875rem;
  font-weight: 700;
  font-size: 1.25rem;
  color: var(--white);
}
.top__heading p {
  color: gray;
  font-weight: 400;
  font-size: 0.9375rem;
}
@media (min-width: 71.875em) {
  .top__heading p {
    font-size: 2.5rem;
  }
}
@media (min-width: 71.875em) {
  .top__heading {
    font-size: 3.125rem;
  }
}
.top__btn {
  margin-top: 1.25rem;
  width: 13.78125rem;
  height: 2.6875rem;
  color: var(--white);
  font-weight: 500;
  border-radius: 1.875rem;
  background: linear-gradient(to right, var(--btn-gradient-1), var(--btn-gradient-2));
  border-style: none;
}
@media (min-width: 71.875em) {
  .top__btn {
    font-size: 1.25rem;
    width: 21.875rem;
    height: 3.75rem;
  }
}

.top__grid {
  margin-top: 6.25rem;
  display: grid;
  row-gap: 4.375rem;
}
@media (min-width: 71.875em) {
  .top__grid {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    gap: 2.5rem 4.375rem;
  }
}
.top__grid .top__grid--access h3 {
  color: #fff;
  margin-top: 1.875rem;
}
.top__grid .top__grid--access h4 {
  color: gray;
}
.top__grid .top__grid--access img {
  display: block;
}

.top__productive {
  display: grid;
  margin-top: 4.375rem;
  margin-bottom: 3.125rem;
}
@media (min-width: 71.875em) {
  .top__productive {
    grid-template-columns: repeat(2, 1fr);
  }
}
.top__productive img {
  width: 100%;
  width: 17.6875rem;
  height: 13.1875rem;
}
@media (min-width: 71.875em) {
  .top__productive img {
    width: 38.125rem;
    height: 29.3125rem;
  }
}
.top__productive--desc {
  margin: auto;
}
.top__productive--desc p:nth-child(1) {
  font-size: 1.5625rem;
  color: #fff;
  font-weight: 400;
  margin-top: 1.875rem;
}
@media (min-width: 71.875em) {
  .top__productive--desc p:nth-child(1) {
    font-size: 3.125rem;
  }
}
.top__productive--desc p {
  color: gray;
}
@media (min-width: 71.875em) {
  .top__productive--desc p {
    font-size: 1.25rem;
  }
}
.top__productive--desc a {
  color: var(--btn-gradient-1);
  text-decoration: underline;
  font-size: 1.25rem;
}
.top__productive--desc a img {
  width: 1.25rem;
  height: 1.25rem;
}

.testimonials {
  margin-top: 2.5rem;
  display: grid;
  color: white;
  gap: 1.875rem 3.125rem;
}
.testimonials p:nth-child(1) {
  padding: 1.125rem;
}
@media (min-width: 71.875em) {
  .testimonials p:nth-child(1) {
    margin: 2.5rem 0.3125rem;
  }
}
.testimonials__1, .testimonials__2, .testimonials__3 {
  position: relative;
  box-sizing: content-box;
  height: 40vh;
  border-radius: 0.625rem;
  background-color: var(--nav-bg);
  box-shadow: 0 0 20px 1px black;
}
.testimonials__1--name, .testimonials__2--name, .testimonials__3--name {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  position: absolute;
  left: 0%;
  bottom: 0;
}
.testimonials__1--name .bold, .testimonials__2--name .bold, .testimonials__3--name .bold {
  font-weight: 700;
}
.testimonials__1--name .grayy, .testimonials__2--name .grayy, .testimonials__3--name .grayy {
  color: gray;
}
.testimonials img {
  width: 5rem;
  height: 5rem;
  margin-left: 2.5rem;
  margin: auto auto;
  border-radius: 50%;
}
@media (min-width: 71.875em) {
  .testimonials {
    grid-template-columns: repeat(3, 1fr);
  }
}

.sub {
  margin-top: 3.125rem;
  background-color: var(--nav-bg);
  border-radius: 0.9375rem;
}
.sub p:nth-child(1) {
  color: #fff;
  padding: 1.8125rem 0 0 0 0;
  font-weight: 700;
}
@media (min-width: 71.875em) {
  .sub p:nth-child(1) {
    font-size: 1.25rem;
    font-weight: 400;
  }
}
.sub p {
  color: gray;
  padding: 0 1.25rem;
}
@media (min-width: 71.875em) {
  .sub p {
    padding: 0 4.375rem;
  }
}
.sub__ask input[type=email] {
  padding: 0.625rem;
  border-style: none;
  border-radius: 10rem;
  margin-bottom: 1.875rem;
}
@media (min-width: 71.875em) {
  .sub__ask input[type=email] {
    width: 40%;
  }
}
.sub__ask--free {
  padding: 0.625rem;
  color: white;
  font-weight: 400;
  border-radius: 10rem;
  font-size: 0.875rem;
  width: 80%;
  border-style: none;
  margin-bottom: 1.25rem;
  background: linear-gradient(to right, var(--btn-gradient-1), var(--btn-gradient-2));
}
@media (min-width: 71.875em) {
  .sub__ask--free {
    width: 30%;
    font-size: 1.25rem;
  }
}

.footer {
  background-color: var(--main-bg);
  display: grid;
  grid-template-rows: repeat(4, 1fr);
  max-height: 60vh;
}
@media (min-width: 71.875em) {
  .footer {
    max-height: 40vh;
    grid-template-columns: repeat(4, 1fr);
  }
}
.footer__firstitem {
  padding: 1.875rem 0;
  color: white;
}
.footer__firstitem span {
  padding-right: 0.5rem;
}
.footer__seconditem {
  color: white;
  color: white;
}
.footer__seconditem span {
  padding-right: 0.5rem;
}
@media (min-width: 71.875em) {
  .footer__seconditem {
    align-self: center;
    padding-left: 2.5rem;
  }
}
.footer__thirditem, .footer__fourthitem {
  color: white;
}
@media (min-width: 71.875em) {
  .footer__thirditem, .footer__fourthitem {
    align-self: center;
    justify-self: center;
    color: #fff;
  }
}